import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import { useSelector } from "react-redux";
import "./Vendor.css";
import { selectUser } from "src/localyStore";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow,
  CButton,
  CModalHeader,
  CModal,
  CModalBody,
  CModalFooter,
} from "@coreui/react";

const Vender = () => {
  const [vendersData, setVendersData] = useState(null);
  const { token } = useSelector(selectUser);

  const history = useHistory();
  const [modal, setModal] = useState(false);

  useEffect(() => {
    axios
      .get(process.env.REACT_APP_SERVER + "/api/vendor/get-vendor-details", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((data) => {
        setVendersData(data.data.vendor);
        // console.log(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [token, modal]);

  const toggle = () => {
    setModal(!modal);
  };

  const vendorCreate = (e) => {
    e.preventDefault();
    const data = e.target;
    const formData = new FormData();
    formData.append("name", data.title.value);
    formData.append("email", data.email.value);
    formData.append("phone", data.phone.value);
    formData.append("url", data.Url.value);
    formData.append("vendorImageLogo", data.img.files[0]);
    for (const [key, value] of formData) {
      console.log(`${key}: ${value}`);
    }
    axios
      .post(process.env.REACT_APP_SERVER + "/api/vendor/add-vendor", formData, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((data) => {
        console.log(data);
        toggle();
      })
      .catch((err) => {
        console.log(err);
        alert("error on  create");
      });
  };

  return (
    <CRow>
      <CCol xl={12}>
        <CCard>
          <CCardHeader>Vendors</CCardHeader>
          <CCardBody>
            <CButton onClick={toggle} color="success">
              Add New Vendors
            </CButton>
            <CDataTable
              items={vendersData}
              fields={[
                { key: "name", _classes: "font-weight-bold" },
                "phone",
                "registered",
                "url",
                "Logo",
              ]}
              hover
              itemsPerPage={50}
              pagination
              clickableRows
              onRowClick={(item) => history.push(`/vendor/${item._id}`)}
              scopedSlots={{
                registered: (item) => <td>{item.updatedAt}</td>,
                Logo: (item) => (
                  <img
                    className="vender_logo"
                    src={item.imageLogo}
                    alt="Logo"
                  />
                ),
              }}
            />
          </CCardBody>
        </CCard>
      </CCol>
      <CModal show={modal} onClose={toggle}>
        <CModalHeader closeButton>Add new Vendor</CModalHeader>
        <CModalBody>
          <form onSubmit={vendorCreate}>
            <div class="form-group">
              <label for="exampleFormControlTextarea1">Vendor Name : </label>
              <input
                required
                name="title"
                type="text"
                class="form-control"
                placeholder="Vendor Name"
              />
            </div>
            <div class="form-group">
              <label for="exampleFormControlTextarea1">Vendor Email : </label>
              <input
                required
                name="email"
                type="email"
                class="form-control"
                placeholder="Vendor Email"
              />
            </div>
            <div class="form-group">
              <label for="exampleFormControlTextarea1">Vendor URL : </label>
              <input
                required
                name="Url"
                type="text"
                class="form-control"
                placeholder="Vendor URL"
              />
            </div>
            <div class="form-group">
              <label for="exampleFormControlTextarea1">
                Vendor Phone No. :{" "}
              </label>
              <input
                required
                name="phone"
                type="text"
                class="form-control"
                placeholder="Vendor Phone NO."
              />
            </div>
            <div class="input-group mb-3 p-1">
              <div class="input-group-prepend">
                <span class="input-group-text" id="inputGroupFileAddon01">
                  Upload
                </span>
              </div>
              <div class="custom-file">
                <input
                  required
                  name="img"
                  type="file"
                  accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
                  class="custom-file-input"
                  id="inputGroupFile01"
                  aria-describedby="inputGroupFileAddon01"
                />
                <label class="custom-file-label" for="inputGroupFile01">
                  Vendor Logo
                </label>
              </div>
            </div>{" "}
            <CModalFooter>
              <CButton type="submit" color="primary">
                Confirm
              </CButton>
            </CModalFooter>
          </form>
        </CModalBody>
      </CModal>
    </CRow>
  );
};

export default Vender;
